body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  width: 1000px;
}

.LetterClicked {
	width: 50px;
	height: 50px;
	font-size: 33px;
	font-weight: bold;
	border: 1px solid #0f0;
	display: inline;
	background-color: #f00;
	color: #fff;
}

.LetterUnclicked {
	width: 50px;
	height: 50px;
	font-size: 33px;
	font-weight: bold;
	background-color: #fff;
	border: 1px solid #0f0;
	display: inline;
}

.rowOfAlphabet {
	width: 250px;
	height: 50px;
	display: block;
}

.Alphabet {
	width: 250px;
	height: 400px;
}

.Lives {
	width: 100px;
	height: 100px;
	border-radius: 10px;
	border: 10px dashed #f00;
	font-size: 66px;
	font-weight: bold;
	background-color: #fff;
	display: -webkit-flex;
	display: flex;
	text-align: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
	margin-right: auto;
	margin-left: auto;
	margin-bottom: 15px;
	margin-top: 15px;
}

.Answer {
	font-size: 50px;
	font-weight: bold;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	text-align: center;
	width: 650px;
	height: 400px;
	background-color: #ff0;
}

.left {
	float: left;
	display: inline;
	-webkit-align-items: center;
	        align-items: center;
	width: 250px;
	height: 600px;
	margin-left: 30px;
}

.right {
	display: block;
	float: right;
	/*align-items: center;
	justify-content: center;
	text-align: center;
	width: 650px;
	height: 600px;
	background-color: #ff0;*/
}

.status {
	font-size: 50px;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	text-align: center;
	width: 650px;
	height: 100px;
	background-color: #fff;
	border: 2px dotted #f00;
	border-radius: 10px;
}



